.header {
  box-sizing: border-box;
  padding: 33px 16px;
  width: 300px;
}
.header__logo {
  display: block;
  margin: 0 auto;
}
.header__navigation {
  margin-top: 33px;
}

.navigation {
  list-style: none;
  padding-left: 0;
}
.navigation__link {
  color: #878787;
  text-decoration: none;
  font-size: 16px;
  line-height: 24px;
  padding: 16px 16px 16px 64px;
  border-radius: 12px;
  display: block;
}
.navigation__link:hover, .navigation__link:focus, .navigation__link:active {
  color: #503e9d;
  background-color: rgba(80, 62, 157, 0.4470588235);
  transition: all 0.5s;
  font-weight: bold;
  text-shadow: 1px 3px 0 #969696;
}
.navigation__link_active {
  color: #503e9d;
  background-color: rgba(80, 62, 157, 0.4470588235);
  transition: all 0.5s;
  font-weight: bold;
  text-shadow: 1px 3px 0 #969696;
}